aside {
  position: fixed;
  top: 0;
  left: -100%;
  width: 30rem;
  max-width: 80%;
  height: 100vh;
  background: rgb(216, 216, 232);
  box-shadow: 0 2 8 #121216;
  transition: left 0.3s ease-out;
  z-index: 100;
}
:host([open]) aside {
  left: 0;
}
header {
  padding: 1rem;
  background: black;
}
header h1 {
  font-size: 1.5rem;
  color: white;
  margin: 0;
}
header button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 1rem;
  color: white;
  background: transparent;
  font-size: 1.5rem;
  border: none;
}
header button:focus {
  outline: none;
}
#tabs {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 1rem 0px;
}
#tabs button {
  width: 30%;
  background: white;
  color: black;
  text-align: center;
  border: 1px solid black;
  font: inherit;
  padding: 0.15em 0;
}

#tabs button.active,
#tabs button:active,
#tabs button:hover {
  background: black;
  color: white;
}
#tabs button:focus {
  border: none;
}
#contact-info {
  padding: 1rem;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3 ease-out;
}
:host([open]) .backdrop {
  opacity: 1;
  pointer-events: all;
}
